import * as GeneralUtils from './general';
import * as ArrayUtils from './array';
import * as BrowserUtils from './browser';
import * as CsvUtils from './csv';
import * as DateUtils from './date';
import * as DomUtils from './dom';
import * as EventUtils from './event';
import * as FunctionUtils from './function';
import * as FuzzyMatchUtils from './fuzzyMatch';
import * as GenericUtils from './generic';
import * as IconUtils from './icon';
import * as KeyboardUtils from './keyboard';
import * as MapUtils from './map';
import * as MouseUtils from './mouse';
import * as NumberUtils from './number';
import * as ObjectUtils from './object';
import * as RowNodeUtils from './rowNode';
import * as SetUtils from './set';
import * as StringUtils from './string';

const utils = {
    ...GeneralUtils,
    ...ArrayUtils,
    ...BrowserUtils,
    ...CsvUtils,
    ...DateUtils,
    ...DomUtils,
    ...EventUtils,
    ...FunctionUtils,
    ...FuzzyMatchUtils,
    ...GenericUtils,
    ...IconUtils,
    ...KeyboardUtils,
    ...MapUtils,
    ...MouseUtils,
    ...NumberUtils,
    ...ObjectUtils,
    ...RowNodeUtils,
    ...SetUtils,
    ...StringUtils,
};

if (NumberUtils.sum([
    GeneralUtils,
    ArrayUtils,
    BrowserUtils,
    CsvUtils,
    DateUtils,
    DomUtils,
    EventUtils,
    FunctionUtils,
    FuzzyMatchUtils,
    GenericUtils,
    IconUtils,
    KeyboardUtils,
    MapUtils,
    MouseUtils,
    NumberUtils,
    ObjectUtils,
    RowNodeUtils,
    SetUtils,
    StringUtils,
].map(x => Object.keys(x).length)) !== Object.keys(utils).length) {
    throw new Error('Functions with the same name are being included from multiple sources into the general utils (_) class');
}

export const _ = utils;